.nav{
    position: fixed;
    top: 0;
    height: 30px;
    z-index: 1;
    width: 100%;
    padding: 20px;
    /* display: flex;
    justify-content: space-between; */
    
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_logo{
    width: 80px;
    object-fit: contain;
    position: fixed;
    left: 20px;
}
.nav_avatar{
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
.nav_black{
    background-color: #111;
}